<template>
<div class="smui">
    <div class="top-nav">
        <span class="comp-name">{{compName}}</span>
        <span>{{desc}}</span>
        <div class="doc-link">
          <a @click="showCode">🍵 &lt;源码&gt; </a>
          <a @click="showParams" v-if="isUIComp">🐜&lt;参数&gt; </a>
          <a @click="switchView" v-if="isUIComp">
          📃 {{viewText}}</a>
        </div>
    </div>
    <div class="mleft">
        <nav-tree :params="config.menu"></nav-tree>
    </div>
    <div class="mright">
        <div v-show="view!='doc'">
        <transition>
            <keep-alive>
                <router-view></router-view>
            </keep-alive>
        </transition>
        </div>
        <div v-show="view=='doc'">
            <doc :comp="comp" ref="doc"></doc>
        </div>
    </div>
</div>
</template>

<script type="text/javascript">
import config from './config'
import field from 'smui/directive/field'
import NavTree from './NavTree.vue'
import {Dialog, Tab} from 'smui'
import CDialog from 'components/Dialog.vue'
import PostForm from 'components/PostForm.vue'
import router from './router'
import CodeEditor from 'components/CodeEditor.vue'
import Doc from 'components/Doc.vue'

export default {
    router: router,
    data () {
      return {
          tab: 'component',
          subTab: 'message',
          config: config,
          view: 'demo',
          viewText: '文档',
          comp: '',
          compName: '',
          needCancel: false,
          params: {
              searchTitle: '',
              treeData: []
          }
      }
    },
    mounted() {
        var $vm = this
        router.beforeEach((to, from, next) => {
            next()
            $vm.comp = to.name
            $vm.view = 'demo'
            $vm.compName = to.name
            var v = document.querySelector('#nav-' + to.name)
            v && v.scrollIntoViewIfNeeded()
        })
        setTimeout(() => {
            this.$refs.doc.comp = location.hash.substr(2)
        }, 100)
    },
    computed: {
        desc () {
            var name = this.$route.name
            this.compName = name;
            var comp = window.routes.find((item) => {return item.name == name})
            if (!comp) {
                return ''
            }
            return comp.component.desc || '';
        },
        isUIComp () {
            return window.SMUI[this.compName]
        }
    },
    methods: {
        initEditor () {
            var name = this.$route.name
            var content = require("raw-loader!mirror/" + name + ".vue")
            var dialog = new Dialog({
                data: {
                    ensureText: '运行',
                    title: name,
                    clazz: 'fixed-dialog'
                },
                content: {
                    extends: CodeEditor,
                    data() {
                        return {
                            source: content
                        }
                    }
                }
            })

            dialog.$on('ensure', () => {
                Dialog.alert('运行实例，敬请期待')
            })

            setTimeout(() => {
                window.editor.scrollIntoView()
            }, 300);
            
            this.$dialog = dialog
        },
        switchView () {
            if (this.view == 'demo') {
                this.view = 'doc'
                this.viewText = '实例'
            }
            else {
                this.view = 'demo'
                this.viewText = '文档'
            }
        },
        showCode () {
            this.initEditor()
            this.$dialog.showModal()
            var dg = this.$dialog.$el.querySelector('.dialog')
            dg.classList.add('f')
        },
        showParams () {
            if (SMUI.hasOwnProperty(this.compName)) {
                var text = SMUI[this.compName].params
                text = JSON.stringify(text, '', 4)
                Dialog.alert({
                    title: this.compName + '参数列表',
                    content: `<pre>${text}</pre>`,
                    width: 600
                })
            }
        }
    },
    components: {
        NavTree, Tab, CDialog, PostForm, Doc
    }
}
</script>
</script>

<style type="text/css" lang="less">
.fixed-dialog {
    .dialog.f {
        position: fixed;
        top:0;
        left: 0;
        height: 100vh;
        width: 100vw;
        overflow: hidden;
        >div>.content {
            padding: 0;
        }
        >div>.footer {
            position: fixed;
            background: #EEE;
            z-index: 99;
        }
    }
}
.top-nav {
    margin-left: 230px;
    height: 45px;
    position: fixed;
    background: #FFF;
    width: 100%;
    z-index: 777;
    left: 0;
    top: 0;
    border-bottom: 1px solid #DDD;
    margin-top: 0;

    .tab-list {
      border: none;
    }
    .tab-list .tab a {
      height: 45px;
    }
    .comp-name {
      font-size: 18px;
      font-weight: bold;
      line-height: 40px;
      padding: 10px 20px;
    }
    .doc-link {
      position: fixed;
      right: 11px;
      top: 13px;
      margin: 0 10px;
      z-index: 9999;
      right: 50px;
      float: right;
      color: #333;
    }
}

.mleft {
  position: fixed;
  top: 0;
  left: 0;
  width: 230px;
  border-right: 1px solid #CCC;
  height: 100vh;
  overflow: hidden;
}

.mleft:hover {
  overflow: scroll;
}

.mright {
  margin-left: 250px;
  margin-top: 80px;
}
.mright  .scrum {
  padding-left: 10px;
  height: 24px;
}
</style>



