<template>
<div>
    <ul class="cssicon-list">
        <li v-for="icon in icons" @click="copyCode(icon)">
            <i :class="icon" class="icon"></i>
            <div class="notation">{{icon}}</div>
        </li>
    </ul>
</div>
</template>

<script type="text/javascript">

import Dialog from 'smui/Dialog'

require('cssicon/css/icons.css')

const icons = ["search","magnify","minify","close","menu","arrow-left","arrow-right","arrow-up","arrow-down","plus","minus","check","remove","trash","audio","pin","chat","stop","clock","clock-h3m30","clock-h9m0","clock-h9m30","profile","bell","heart","print","edit","link","map","mail","bookmark","camera","focus","card","lock","unlock","code","flag","tag","signal","export","upload","import","download","upload2","shutdown","paperclip","retweet","key","key2","refresh","pilcrow","justified","paragraph-direction","left-align","center-align","right-align","indent","outdent","mouse","keyboard","left-double-quote","right-double-quote","left-single-quote","right-single-quote","mustache","eye","battery-0","battery-1","battery","battery-3","suitcase","browser","tv","laptop","mobile","tablet","picture","watch","target","navigate","float","more","more-vertical","smile","wink","wink-tougue","tougue","relieved","laugh","wink-laugh","grin","grin-wink","smiley","wink-smiley","relieved-smiley","sad","pensive","unamused","cloud","snow","rain","cloud-raindrop","cloud-lightning","cloud-download","cloud-upload","moon","sun","sun-brightness","sun-raindrop","sun-horizon","sunrise","sunset","raindrop","no-raindrop","search-solid","magnify-solid","minify-solid","trash-solid","audio-solid","pin-solid","chat-solid","stop-solid","clock-solid","clock-h3m30-solid","clock-h9m0-solid","clock-h9m30-solid","profile-solid","bell-solid","heart-solid","print-solid","edit-solid","mail-solid","bookmark-solid","camera-solid","card-solid","lock-solid","unlock-solid","flag-solid","tag-solid","key-solid","key2-solid","pilcrow-solid","mouse-solid","keyboard-solid","left-double-quote-solid","right-double-quote-solid","left-single-quote-solid","right-single-quote-solid","mustache-solid","eye-solid","eye-solid2","eye-solid3","battery-solid-0","battery-solid-1","battery-solid","battery-solid-3","suitcase-1","suitcase-2","suitcase-3","browser-solid","tv-solid","laptop-solid","mobile-solid","tablet-solid","picture-solid","watch-solid","target-solid","navigate-solid","float-solid","more-solid","more-vertical-solid","smile-solid","tougue-solid","relieved-solid","laugh-solid","grin-solid","smiley-solid","relieved-smiley-solid","sad-solid","pensive-solid","unamused-solid","cloud-solid","cloud-download-solid","cloud-upload-solid","moon-solid","sun-solid","sun-raindrop-solid","sun-horizon-solid","sunrise-solid","sunset-solid","raindrop-solid"]

export default {
    data() {
        return {
            icons: icons
        }
    },
    methods: {
        copyCode(iconName) {
            let str = `<i class="icon ${iconName}"></i>`
            Dialog.alert(str)
        }
    }
}
</script>

<style type="text/css" lang="less">
.cssicon-list {
    .icon,
    .icon * {
        box-sizing: content-box !important;
    }

    li {
        display: inline-block;
        padding: 10px;
        width: 150px;
        height: 85px;
        margin: 10px;
        text-align: center;
        margin: 10px 20px;
        vertical-align: top;
        border: 1px solid #EEE;
        position: relative;
        cursor: pointer;
    }

    .notation {
        margin-top: 30px; 
        text-align: center;
    }

    .xicon {
        display: none;
        position: absolute;
        color: #26C890;
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        background: #FFF;
        text-align: center;
        line-height: 50px;
        font-size: 3em;
    }

    li:hover {
        .xicon {
            display: block;
        }
    }
}
</style>



