<template>
<td data-col="select" class="row-select head-cell" 
    @mouseover="selectHover = true">
    <div class="cell">
        <input type="checkbox" class="select-all" 
            v-model="checkAll" :checked="checkAll"
            @click="selectAll"/>
        <div v-if="checkAll && needCheckAllPages" 
            class="select-flip"
            v-show="selectHover"
            @mouseout="selectHover = false">
            <label for="checkall-ipt">
                <input type="checkbox" class="select-all" id="checkall-ipt"
                v-model="checkAll" :checked="checkAll"
                @click="selectAll">
                选择本页</label>
            <label>
                <input type="checkbox" v-model="checkAllPages"
                    :checked="checkAll && checkAllPages"
                    @click="checkAllPages == true">选全部
            </label>
        </div>
    </div>
</td>
</template>

<script type="text/javascript">
import ui from '../base/ui'

const params = {
    selected: [],
    items: [],
    needCheckAllPages: false,
    selectHover: false,
    checkAllPages: false,
    indeterminate: false,
    checkAll: false
}

export default {
    mixins: [ui],
    params: params,
    watch: {
        indeterminate (value) {
            var $all = this.$el.querySelectorAll('.select-all')
            for (let item of $all) {
                item.checked = value === 2
                item.indeterminate = value === 1
            }
        },
        selected (value) {
            if (!this.selected) {
                return
            }
            if (this.selected.length === 0) {
                this.indeterminate = 0
                return false
            }
            else if (this.selected.length === this.items.length) {
                this.indeterminate = 2
                return true
            }
            else if (this.selected.length < this.items.length) {
                this.indeterminate = 1
                return false
            }
        }
    },
    methods: {
        selectAll() {
            setTimeout(()=>{
                this.$emit('check-change', 
                    {
                        checkAll: this.checkAll,
                        checkAllPages: this.checkAllPages
                    }
                )
            }, 100);
        }
    }
}
</script>

<style type="text/css" lang="less">
td.row-select {
    .cell {
        z-index: 99;
    }
    .select-flip {
        position: absolute;
        z-index: 9;
        width: 80px;
        height: 75px;
        padding: 6px;
        line-height: 2rem;
        border: 1px solid #DDD;
        top: -1px;
        left: -1px;
        border-radius: 2px;
        box-shadow: 2px 2px #CCC;
        background-color: #EEE;
        label {
            width: auto!important;
            display: block;
        }
    }
}
</style>


