<template>
<div class="stepper-demo">
    <h1>步骤进度提示</h1>
    <div class="box">
        <ul>
            <stepper :params="params"
                @value-change="changeStep"
                ref="stepper"></stepper>
        </ul>
    </div>
    <div class="box">
        <button @click="params.value > 1 && params.value--">上一步</button>
        <button @click="params.value < 3 && params.value++">下一步</button>
    </div>
</div>
</template>
<script type="text/javascript">
import Dialog from 'smui/Dialog'
import Stepper from 'smui/Stepper'

const steps = [
    {name: 'step one'},
    {name: 'step two'},
    {name: 'step three'}
]

export default {
    data() {
        return {
            params: {
                value: 1,
                items: steps
            }
        }
    },
    methods: {
        changeStep (e) {
            Dialog.toast({message: e.data.value})
        }
    },
    components: {
        Stepper
    }
}
</script>

<style type="text/css" lang="less">
.stepper-demo {
    .box {
        margin: 20px 0;
    }
}  
</style>


