<template>
<div class="smui-progress">
    <span v-if="showValue">{{value}} {{unit}}</span>
    <div class="progress-box" 
        @click="changeValue" v-if="type == 'custom'">
        <div class="bar" :style="{width: ratio, height: height+'px'}"></div>   
    </div>
    <progress :value="value" :max="max" 
        @change="changeValue" v-show="type == 'native'"/>
</div>
</template>

<script type="text/javascript">
import ui from '../base/ui'

const params = {
    value: 0,
    height: 10,
    unit: '',
    showValue: false,
    type: 'custom',
    disabled: false,
    ratio: 0,
    max: 100
}

export default {
    mixins: [ui],
    params: params,
    computed: {
        ratio() {
            return Math.floor(this.value * 100 / this.max) + '%'
        }
    },
    methods: {
        changeValue (e) {
            if (this.disabled) {
                return false
            }
            if (e) {
                let el = this.$el.querySelector('.progress-box')
                this.value = (e.offsetX * this.max / el.clientWidth).toFixed(2)
            }
            this.emit('change-value', {value: this.value});
        }
    }
}
</script>

<style type="text/css" lang="less">
.smui-progress {
    progress {
        width: 160px;
        height: 20px;
        border: 1px solid #0064B4;  
        background-color:#e6e6e6;
        color: #0064B4; /*IE10*/
    }
    progress::-moz-progress-bar { background: #0064B4;}
    progress::-webkit-progress-bar { background: #FFF;}
    progress::-webkit-progress-value  { background: #0064B4;}
    .bar {
        background-color: deepskyblue;
        height: 10px;
        border-radius: 5px; 
    }
    .progress-box {
        border: 1px solid #EEE;
        border-radius: 5px;
        background-color: #EEE; 
        width: 100%;
        display: block;
    }
}
</style>


