<template>
<div class="code-editor">
    <textarea id="text-code">
        {{source}}
    </textarea>
    <div class="result-panel">
        <pre>{{output}}</pre>
    </div>
</div>
</template>

<script type="text/javascript">
import * as Babel from 'babel-standalone'
import LineBox from './LineBox'
import CodeMirror from 'codemirror/lib/codemirror'   
import 'codemirror/lib/codemirror.css'
import 'codemirror/mode/css/css'
import 'codemirror/mode/xml/xml'
import 'codemirror/mode/vue/vue'
import 'codemirror/addon/mode/overlay'
import 'codemirror/mode/javascript/javascript'
import 'codemirror/mode/htmlmixed/htmlmixed'

// var fileCode = require("raw-loader!mirror/Calendar.vue");


window.compiler = {
    getBlock (content, block) {
        let regs = new RegExp(`<${block}[^>]*>([.\\D\\W\d\\w\\s]*)</${block}>`)
        let matches = content.match(regs)
        if (matches && matches.length > 1) {
            return matches[1]
        }
    },
    compile (content) {
        let result = {
            template: this.getBlock(content, 'template'),
            script: this.getBlock(content, 'script'),
            style: this.getBlock(content, 'style')
        }
        if (!result.script && !result.template) {
            result.script = content
        }
        return result
        // let tdiv = document.querySelector('#temp-div')
        // let preview = document.querySelector('#preview-app')
        // preview.innerHTML = '<div class="container"></div>'
        // if (result.template) {
        //     tdiv.querySelector('.tpl').innerHTML = result.template;
        // }
        // if (result.script) {
        //     let conf = {
        //         el: '#preview-app .container',
        //         template: result.template
        //     }
        //     var module = {
        //         exports: {}
        //     }
        //     var exports = module.exports
        //     var require = window.req
        //     eval(result.script)
        //     let obj = new Vue(
        //         Object.assign(conf, module.exports)
        //     )
        // }
        // if (result.style) {
        //     tdiv.querySelector('style').textContent = result.template;
        // }
    }
}

export default {
    props: ['content'],
    data() {
        return {
            source: '',
            output: ''
        }
    },
    watch: {
        content(value) {
            this.source = value
            this.compileCode()
        }
    },
    mounted() {
        this.initEditor()
    },
    methods: {
        compileCode (e) {
            // this.source = e.data.content
            let source = compiler.compile(this.source)
            try {
                source.script = Babel.transform(
                    source.script, {presets: ["es2015"]}
                ).code

                source.script += '\nexports.template=\"'+ source.template + '\"\n'

                this.output = `(function (require, exports, module) {\n${source.script}\n})(require, exports, module)`
            }
            catch(e) {
                this.output = ''
            }
            
        },
        initEditor () {
            var $vm = this

            debugger
            var mixedMode = {
                name: 'htmlmixed',
                scriptTypes: [{
                    matches: /\/x-handlebars-template|\/x-mustache/i,
                    mode: null
                }]
            }
            
            var textCode = document.getElementById("text-code")
            this.codeContent = textCode.value = textCode.textContent.trim()
            window.textCode = textCode
            var editor = CodeMirror.fromTextArea(
                textCode,
                {
                    mode: mixedMode,
                    selectionPointer: true,
                    lineNumbers: true,
                    tabSize: 4,
                    matchBrackets: true,
                    styleActiveLine: false
                }
            )

            editor.on('change', function() {
                $vm.source = editor.getValue()
                $vm.compileCode()
            })
            $vm.compileCode()
        }
    },
    components: {
        LineBox
    }
}
</script>

<style type="text/css" lang="less">
.code-editor {
    .result-panel {
        display: none;
    }
}

</style>


