<template>
<div class="line-box-demo demo">
    <div class="section">
        <h2 class="demo-title">消息提醒</h2>
        <div class="box">
            <button @click="alert">alert</button>
            <button @click="confirm">confirm</button>
            <button @click="prompt">prompt</button>
            <button @click="popup">popup</button>
        </div>
    </div>

    <div class="section">
        <h2 class="demo-title">Toast</h2>
        <div class="box">
            <button @click="toast('tip')">toast</button>
            <button @click="toast('info')">info</button>
            <button @click="toast('warm')">warming</button>
            <button @click="toast('error')">error</button>
        </div>
        <div class="box list">
            <div class="toast tip">tip</div>
            <div class="toast info">info</div>
            <div class="toast warm">warm</div>
            <div class="toast error">error</div>
        </div>
    </div>

    <div class="section">
      <h2 class="demo-title">加载指示</h2> 
      <div class="box">
          <button @click="loading">加载</button>
      </div> 
    </div>

</div>
</template>

<script type="text/javascript">
import Dialog from 'smui/Dialog'

export default {
    data() {
        return {
            canBatch: false,
            params: {
                content: ''
            }
        }
    },
    methods: {
        loading() {
            Dialog.loading()
        },
        alert() {
            Dialog.alert('alert')
        },
        confirm() {
            Dialog.confirm('confirm')
        },
        prompt() {
            Dialog.prompt('prompt')
        },
        toast(type) {
            Dialog.toast({
                message: '消息' + (type || ''),
                clazz: [type]
            })
        },
        popup() {
            Dialog.popup({
                title: '喜大普奔！',
                message: '普天同庆！'
            })
        },
    }
}
</script>

<style type="text/css" lang="less">
.box.list .toast {
    position: static;
    display: inline-block;
    width: 80px;
}
</style>


