import Vue from 'vue'
import VueRouter from 'vue-router'
import * as smui from 'smui'
import config from './config'
import Home from 'components/Home'

Vue.use(VueRouter)

const rmap = {}

const routes = [{
    path: '/',
    component: Home
}]

Object.assign(rmap, SMUI)

config.menu.forEach((cate)=> {
    for (let item of cate.children) {
        rmap[item.name] = item
    }
})

for (let compName in rmap) {
    routes.push(
        {
            name: compName,
            path: '/' + compName,
            component (resolve) {
                require(['components/' + compName], resolve)
            } 
        }
    )
}

window.routes = routes

window.router = new VueRouter({
    routes: routes
})

module.exports = window.router


