<template>
<div class="tab-demo demo">
<div class="section">
    <h2 class="demo-title">Tab 卡片标签</h2>
    <div class="box">
        <md>
            用途：切换不同视图模块
        </md>
    </div>
</div>

<div class="section">
    <h2 class="demo-title">Tab例子</h2>
    <tab :params="params"
            @tab-change="changeValue"></tab>
    <div class="box">
        {{info}}
    </div>
</div>

</div>
</template>

<script type="text/javascript">
import Tab from 'smui/Tab'

const tabList = [
    {name: '表格', tab: 'table'},
    {name: '表单', tab: 'form'},
    {
        name: '消息',
        tab: 'dialog',
        subTab: 'message',
        children: [
            {name: '消息', tab: 'message'},
            {name: '弹窗', tab: 'dialog'}
        ]
    }
]

export default {
    data() {
        return {
            info: '',
            params: {
                items: tabList
            }
        }
    },
    methods: {
        changeValue (e) {
            this.info = e.data
        }
    },
    components: {
        Tab
    }
}
</script>



