	module.exports = "<template>\n<div class=\"pager-demo demo\">\n    <div class=\"section\">\n        <h2 class=\"demo-title\">翻页组件</h2>\n        <div class=\"box\">\n        <md>\n        ### Pager 支持功能\n        - 前端翻页：传入数组自动分页\n        - 后端翻页：每次请求后端更新翻页信息\n        - 定制页面大小\n        - 跳转指定页面\n        </md>\n        </div>\n    </div>\n\n    <div class=\"section\">\n        <h2 class=\"demo-title\">Pager</h2>\n        <div class=\"box\">\n            <rich-table :params=\"tableConf\" ref=\"table\"></rich-table>\n            <pager :params=\"pageInfo\"\n                @list-change=\"changeList\"\n                @page-change=\"changePage\"\n                @size-change=\"changePage\">  \n            </pager>\n        </div>\n    </div>\n</div>\n</template>\n\n<script type=\"text/javascript\">\n\nvar Dialog = require('smui/Dialog')\nvar RichTable = require('smui/RichTable')\nvar Pager = require('smui/Pager')\n\nvar list = new Array(100).fill(\n    {\n        name: 'jack',\n        score: 0,\n        grade: 'A'\n    }\n).map((item, idx) => {\n    return Object.assign({}, item, {score: idx % 97})\n});\n\n// 表格配置\nvar tableConf = {\n    fields: ['name', 'score', 'grade'],\n    items: list.slice(0, 10),\n    headTextMap: {\n        name: '姓名',\n        score: '成绩',\n        grade: '等级'\n    },\n    cellTextMap: {\n        grade (value, item) {\n            // 动态计算等级取值\n            var grades = ['A', 'B', 'C', 'D'].reverse()\n            return grades[ Math.floor(item.score / 30) ]\n        }\n    }\n}\n\nmodule.exports = {\n    data() {\n        var pageInfo = {\n            currentPage: 1,\n            pageSize: 10,\n            sizeOptions: [10, 20, 50],\n            items: list,\n            totalSize: list.length\n        }\n\n        return {\n            tableConf: tableConf,\n            pageInfo: pageInfo\n        }\n    },\n    methods: {\n        changePage (e) {\n            var data = e.data;\n            var info = this.pageInfo;\n            // 更新翻页信息\n            Object.assign(info, data);\n        },\n        changeList (e) {\n            // 只有前端列表翻页才需要传入items\n            var data = e.data;\n            this.$refs.table.setItems(data.list);\n            this.$refs.table.setSelected([]);\n        }\n    },\n    components: {\n        Pager,\n        RichTable\n    }\n}\n</script>"

