<template>
<div class="loading-box"
    :class="{mask: showMask, full: isFull}"
    v-show="isShow">
    <i :class="icon" class="icon" v-if="icon"></i>
    <div class="content">
        <slot>{{content}}</slot>
    </div>
</div>

</template>
<script type="text/javascript">
import ui from '../base/ui'

export default {
    mixins: [ui],
    params: {
        isShow: false,
        isFull: false,
        needMask: true,
        showMask: false,
        content: '',
        timeout: 10000,
        delay: 500,
        icon: 'sm-loading',
    },
    methods: {
        show (option) {
            if (option) {
                Object.assign(this, option);
            }
            this.isShow = true;
            this.isFull = true;
            setTimeout(()=> {this.close()}, this.timeout);

            var $vm = this;
            this.isShow = true;
            
            // 故意延迟再展现mask效果，避免不必要遮罩
            if (this.needMask) {
                setTimeout(() => {
                    $vm.showMask = true;
                }, this.delay);
            }
        },
        close () {
            if (this.$el) {
                this.$el.remove();
            }
        }
    }
}

</script>

<style type="text/css" lang="less">
.loading-box.mask {
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.2);
}

.loading-box.full {
    position: absolute;
    width: 100%;
    height: 100%;
    position: fixed;
    .content {
        position: absolute;
        top: 50%;
        left: 49vw;
    }
}

.icon-snake {
    border: 4px solid transparent;
    border-top-color: #26a2ff;
    border-left-color: #26a2ff;
    border-bottom-color: #26a2ff;
    height: 28px;
    width: 28px;
    display: inline-block;
    border-radius: 50%;
}

.loading-box {
    z-index: 10001;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    position: relative;
    min-height: 5em;
    vertical-align: top;
    background: rgba(0,0,0,0.01);

    .close {
        font-size: 1.5em;
    }
    i.icon {
        font-size: 3em;
        color: #26a2ff;
        margin-left: -20px;
        margin-top: -20px;
    }
    div.icon {
        width: 100%;
        left: 0;
        text-align: center;
    }

    .icon {
        position: absolute;
        left: 50%;
        top: 50%;
        animation: loading 1s infinite step-start;
    }

    .content {
        text-align: center;
        margin-top: 6em;
    }

    .close {
        position: absolute;
        top: 5px;
        right: 5px;
    }
    @keyframes loading {
        .loading-anime(7);
    }
    .loading-anime(@i) when(@i >= 0) {
        .loading-anime(@i - 1);
        @p: 12.5 * @i;
        @selector: e('@{p}%');
        @{selector} {
            transform: rotate(45deg * @i);
        }
    }
}
</style>



