<template>
<div class="info-demo demo">
    <div class="section">
      <h2 class="demo-title">Info 信息提示</h2>
      <div class="box">
          <md>
            ### 提供不同方向的气泡提示
            - 通过指定derection参数

            ```js
            {isShow: true, hideOnLeave: false, direction: 'left'}
            ```
          </md>
      </div> 
      <div class="box info-box">
          <info :params="{width: '100px', isShow: true, hideOnLeave: false, direction: 'left'}">
            向左气泡 {direction: 'left'}
          </info>
          <info :params="{width: '100px', isShow: true, hideOnLeave: false, direction: 'right'}">
            向右气泡 {direction: 'right'}
          </info>
      </div>

      <div class="box info-box">
          <info :params="{width: '100px', isShow: true, hideOnLeave: false, direction: 'top'}">
            向上气泡 {direction: 'left'}
          </info>

          <info :params="{width: '100px', isShow: true, hideOnLeave: false, direction: 'bottom'}">
            向下气泡 {direction: 'left'}
          </info>
      </div>

      <div class="box">
          <md>
            # 自动隐藏，点击或者鼠标经过时显示
            - 内容默认是可以插入任意html内容
            - 通过slot注入内容

            ```html 
            ＜info :params="{direction:'left'}"＞信息提示＜/info＞
            ```
          </md>

          <info :params="{direction:'right'}">信息提示</info>
      </div>

      <div class="box info-box">
          <info>
            <md>
              ## 一条信息有多重要
              - 第一
              - 第二
            </md>
          </info>
      </div> 
    </div>
</div>
</template>

<script type="text/javascript">
import Info from 'smui/Info'

export default {
    data() {
        return {
            canBatch: false,
            params: {
                content: ''
            }
        }
    },
    components: {
        Info
    }

}
</script>

<style type="text/css" lang="less">
.info-demo {
    .info-box {
        display: flex;
        margin: 50px;
        .info-sign {
          position: relative;
          margin-left: 100px;
          width: 200px;
          height: 200px;
          display: inline-block;
      }
    }
}
</style>


