<template>
<div class="tab-demo demo">
<div class="section">
    <h2 class="demo-title">Tab 卡片标签</h2>
    <div class="box">
        <md>
            用途：切换不同视图模块
        </md>
    </div>
</div>

<div class="section">
    <h2 class="demo-title">Tab 片标签</h2>
    <press-tab :params="params"
            @tab-change="changeValue"></press-tab>
    <div class="box">
        {{info}}
    </div>
</div>

</div>
</template>

<script type="text/javascript">
import PressTab from 'smui/PressTab'

const tabList = [
    {text: '计划', name: 'plan'},
    {text: '单元', name: 'unit'},
    {text: '关键词', name: 'keyword'}
]

export default {
    data() {
        return {
            info: '',
            params: {
                tab: 'plan',
                items: tabList
            }
        }
    },
    methods: {
        changeValue (e) {
            this.info = e.data
        }
    },
    components: {
        PressTab
    }
}
</script>



