<template>
<div class="nav-list">
    <div class="nav-title">
        <a href="#/">前端组件规范</a>
    </div>
    <ul class="router-list">
        <li v-for="topic in params">
            <h2 @click="toggle(topic)">{{topic.title}} <i :class="topic.icon || 'sm-arrow-down'"></i></h2>
            <ul v-show="topic.show">
                <li v-for="item in topic.children" :id="'nav-'+item.name">
                    <router-link v-bind:to="item.name">
                        <b>{{item.name}}</b>
                        <span>{{item.cnName}}</span>
                    </router-link>
                </li>
            </ul>
        </li>
    </ul>
</div>
</template>

<script type="text/javascript">
export default {
    props: ['params'],
    data() {
        return {
            
        }
    },
    methods: {
        toggle(item) {
            item.show = !item.show
            item.icon = item.show ? 'sm-arrow_up' : 'sm-arrow-down'
        }
    }
}
</script>

<style type="text/css" lang="less">

.nav-list {
    background-color: #21364D;
    line-height: 20px;
    color: #FFF;
    min-height: 100vh;
    border-right: 2px outset #0C2A4B;

    .nav-title {
        height: 45px;
        line-height: 45px;
        padding-left: 16px;
        background-color: #0C2A4B;
        font-size: 20px;
        line-height: 45px;
        color: #FFF;
        position: fixed;
        width: 229px;
        z-index: 99;
        top: 0;
        left: 0;
    }

    a {
        color: #CCC;
    }

    .router-list {
        padding-top: 45px;
        a {
            color: #CCC;
            display: block;
            font-size: 12px;
            line-height: 40px;
            padding-left: 40px;
        }

        a:hover,
        a.router-link-active,
        a.active {
            background-color: #2E4656;
            color: #08f18c;
            border-right: 2px solid #15dc86;
            margin-right: -2px;
            span {
                display: inline;
            }
        }
    }
    

    b {
        min-width: 8em;
        display: inline-block;
    }

    a span {
        color: #08f18c;
        display: none;
    }

    h2 {
        line-height: 30px;
        padding-left: 10px;
        margin: 10px 0 0 0;
        color: #CCC;
        font-size: 14px !important;
        position: relative;

        i {
            margin-right: 10px;
            position: absolute;
            font-size: 10px;
            right: 10px;
            top: 10px;
            transform: scale(0.7);
        }
    }
}
</style>


