<template>
<div class="city-picker-demo demo">
    <div class="section">
      <h2 class="demo-title">城市选择</h2> 
      <div class="box">
          <city-picker :params="params"></city-picker>
      </div> 
    </div>
</div>
</template>

<script type="text/javascript">
import CityPicker from 'smui/CityPicker'

export default {
    data() {
        return {
            params: {
                province: 4,
                city: 5
            }
        }
    },
    components: {
        CityPicker
    }
}
</script>


