module.exports = "<template>\n<div class=\"calendar-demo demo\">\n    <div class=\"section\">\n        <h2 class=\"demo-title\">单日期选择</h2>\n        <div class=\"box\">\n            <div>{{singleDate}}</div>\n            <calendar :params=\"singleConf\"\n                @value-change=\"changeSingleDate\"></calendar>\n            <p class=\"params\">\n                <pre>{{singleConf}}</pre>\n            </p>\n        </div>\n    </div>\n    <div class=\"section\">\n        <h2 class=\"demo-title\">时间点选择</h2>\n        <div class=\"box\">\n            <div>{{datetime}}</div>\n            <calendar :params=\"calConf\"\n                @value-change=\"changeDate\" ref=\"dtimer\"></calendar>\n            <p class=\"params\">\n                <pre>{{calConf}}</pre>\n                <button @click=\"changeFormat('YYYY-MM-DD')\">只显示日期</button>\n                <button @click=\"changeFormat('YYYY-MM-DD HH:mm')\">不显示秒</button>\n                <button @click=\"changeFormat('YYYY-MM-DD HH')\">不显示分</button>\n                <br/>时间格式：<br/>\n                <input type=\"text\" v-model=\"calConf.format\" @change=\"changeFormat('')\" name=\"fformate\" size=\"30\"/>\n            </p>\n        </div>\n    </div>\n    <div class=\"section\">\n        <h2 class=\"demo-title\">月份选择</h2>\n        <div class=\"box\">\n          <month-select></month-select>  \n        </div>\n        <div class=\"box\">\n          <month-picker></month-picker>  \n        </div>\n    </div>\n</div>\n</template>\n\n<script type=\"text/javascript\">\nimport Calendar from 'smui/Calendar'\nimport MonthPicker from 'smui/Calendar/MonthPicker'\nimport MonthSelect from 'smui/Calendar/MonthSelect'\n\nexport default {\n    desc: '日历组件支持选择时间、日期、月份',\n    data() {\n        var date = '2016-12-24'\n        var datetime = '2016-12-24 10:08'\n\n        return {\n            singleDate: date,\n            datetime: datetime,\n            singleConf: {\n                value: date,\n                type: 'date',\n                y: 56,\n                show: true\n            },\n            calConf: {\n                value: datetime,\n                format: 'YYYY-MM-DD HH:mm:ss',\n                y: 56,\n                show: true\n            }\n        }\n    },\n    components: {\n        Calendar,\n        MonthPicker,\n        MonthSelect\n    },\n    methods: {\n        changeSingleDate(e) {\n            this.singleDate = e.data.value\n            this.singleConf.value = e.data.value\n        },\n        changeDate(e) {\n            this.datetime = e.data.value\n            this.calConf.value = this.datetime\n        },\n        changeFormat(value) {\n            if (value) {\n                this.calConf.format = value;\n            }\n            this.$refs.dtimer.format = this.calConf.format;\n            this.$refs.dtimer.updateType()\n        }\n    }\n}\n</script>\n\n<style type=\"text/css\" lang=\"less\">\n.calendar-demo {\n    h2 {\n        font-size: 16px;\n        line-height: 30px;\n        font-weight: bold;\n    }\n\n    .box {\n        position: relative;\n        min-height: 350px;\n        margin: 20px;\n    }\n\n    .params {\n        position: absolute;\n        right: 20px;\n        width: 300px;\n        height: 300px;\n        text-align: left;\n    }\n\n    li {\n        line-height: 30px;\n        color: #FFF;\n        padding: 10px;\n        margin: 25px auto;\n        width: 78%;\n        border-radius: 2px;\n    }\n}\n</style>\n"


