<template>
<div class="date-picker-demo demo">
    <div class="section">
        <h2 class="demo-title">日期范围选择</h2>
        <div class="box">
            <date-picker
                :params="dateRange"
                @range-change="changeRange"> 
            </date-picker>
        </div>
    </div>
</div>
</template>

<script type="text/javascript">
import DatePicker from 'smui/DatePicker'

window.DatePicker = DatePicker
export default {
    desc: '日历组件支持选择时间、日期、月份',
    data() {
        var range = '2016-12-04~2016-12-24'
        return {
            range: range,
            dateRange: {
                value: range
            }
        }
    },
    components: {
        DatePicker
    },
    methods: {
        changeRange(e) {
            this.range = e.data.value
        }
    }
}
</script>

<style type="text/css" lang="less">
.date-picker-demo {
    .date-picker {
        line-height: 30px;
    }
}
</style>



