<template>
<div class="loading-demo demo">
    <div class="section">
      <h2 class="demo-title">loading旋转样式</h2> 
      <div class="box">
          <loading :params="{isShow: true}"></loading>
          <loading :params="{isShow: true, content: 'loading'}"></loading>
          <loading :params="{isShow: true, icon: 'icon-snake'}">加载...</loading>
      </div> 
    </div>

    <div class="section">
      <h2 class="demo-title">事件触发加载</h2> 
      <div class="box">
          <md>
          ```js
          Dialog.loading({
              icon: 'sm-loading',
              content: '加载中...'
          })
          ```
          </md>
      </div>
      <div class="box">
          <button @click="loading">加载</button>
      </div> 
    </div>

    <div class="section">
      <h2 class="demo-title">加载指示</h2> 
      <div class="box">
          <md>
          ```js
          {
              isShow: false, // 显示状态
              isFull: false, // 是否全屏
              needMask: true, // 是否需要锁屏
              showMask: false, // 是否显示遮罩
              content: '',     // 文本内容
              timeout: 10000,  // 超时自动关闭
              delay: 500,      // 默认显示事件
              animal: 'spining', // 动画方案
              icon: 'sm-loading' // 默认图标
          }
          ```
          </md>
      </div> 
      <div class="box">
          <button @click="loading">加载</button>
      </div> 

    </div>

</div>
</template>

<script type="text/javascript">
import Dialog from 'smui/Dialog'
import Loading from 'smui/Dialog/Loading'

export default {
    data() {
        return {
            canBatch: false,
            params: {
                content: ''
            }
        }
    },
    components: {
        Loading
    },
    methods: {
        loading() {
            Dialog.loading()
        }
    }
}
</script>

<style type="text/css" lang="less">
.loading-demo {
    .loading-box {
        width: 100px;
        height: 100px;
        display: inline-block;
    }
}
</style>


