<template>
<div class="city-picker-demo demo">
    <div class="section">
      <h2 class="demo-title">Schedule 时段规划</h2> 
      <div class="box">
          <md>
          用途：用于推广时段选择

          - 支持小时粒度的时间规划
          - 支持快捷选项
          </md>
      </div> 
    </div>
    <div class="section">
      <h2 class="demo-title">时段选择</h2> 
      <div class="box">
          <schedule :params="params"></schedule>
      </div> 
    </div>
</div>
</template>

<script type="text/javascript">
import Schedule from 'smui/Schedule'

export default {
    data() {
        return {
            params: {
                province: 4,
                city: 5
            }
        }
    },
    components: {
        Schedule
    }
}
</script>


