<template>
<thead class="head-row">
    <tr>
        <row-select :params="params"
            @check-change="changeCheck"
            v-if="selectable" ref="rowSelect">     
        </row-select>
        <td v-for="field in fields" :data-col="field" class="head-cell">
            <div class="cell">
                <div v-html="headFilter(field)" 
                    :style="{width: fieldWidth[field] || minWidth}"></div>
                <div class="plugin-items" v-html="headPlugin(field)"></div>
            </div>
        </td>
    </tr>
</thead>
</template>

<script type="text/javascript">
import ui from '../base/ui'
import RowSelect from './RowSelect'

const params = {
    fieldWidth: {},
    selectable: true,
    headTextMap: {},
    minWidth: '100px',
    fields: []
}

export default {
    mixins: [ui],
    params: params,
    components: {
        RowSelect
    },
    methods: {
        changeCheck(e) {
            debugger
            this.$emit('check-change', e)
        },
        headFilter (field) {
            if (this.headTextMap) {
                return this.headTextMap[field] || field;
            }
            else {
                return field;
            }
        },
        headPlugin (field) {

        }
    }
}
</script>

<style type="text/css" lang="less">
.head-mask {
    height: 35px;
    width: 100%;
    background: #EEE;
    position: absolute;
    top: 0;
    z-index: 1;
}
.scrolling .head-cell {
    position: static!important;
    .cell {
        position: absolute;
        line-height: 30px;
        z-index: 3;
        top: 0;
    }
}
</style>


