import Vue from 'vue'
import App from './main.vue'
import Dialog from 'smui/Dialog'
import MarkPreview from 'components/MarkPreview'

Vue.component('md', MarkPreview)

window.Dialog = Dialog

window.Vue = Vue
require('smui/style/skin.less')
require('resource/css/main.less')
require('resource/css/demo.less')

let app = new Vue({
    extends: App
}).$mount('#main')


