<template>
<div class="smui-table simple-table">
    <div @click="click">
        <div class="table-body" :class="{scroll: scrollable, scrolling: scrollTop > 10}" @scroll="lockHeader">
            <div class="head-mask" v-if="scrollTop > 10"></div>
            <table>
            <table-head :params="params"
                @check-change="changeCheck" ref="head"></table-head>
            <tr v-for="(item, row) in items" class="table-row">
                <td v-if="selectable" data-col="select">
                    <input type="checkbox" v-model="selected" :value="row"/>
                </td>
                <td v-for="field in fields"
                    :data-col="field" :data-row="row">
                    <div v-html="cellFilter(field, item)"></div>
                    <div class="plugin-items" v-html="cellPlugin(field, item)"></div>
                </td>
            </tr>
            </table>
        </div>
    </div>
</div>
</template>

<script type="text/javascript">

import Vue from 'vue'
import ui from '../base/ui'
import TableHead from './TableHead'

// 接口属性
var params = {
    cellTextMap: {},
    headTextMap: {},
    selected: [],
    fields: [],
    items: [],
    info: {},
    scrollable: false,
    fieldWidth: {}, // 列宽配置
    selectable: true, // 是否可以行选择
    needCheckAllPages: false, // 是否支持全部页选择
    headRow: ['origin'] // 支持多个表头配置
}

var globalTimer = 0

export default {
    params: params,
    mixins: [ui],
    private: {
        scrollTop: 0
    },
    ready () {
        this.fixHeadWidth()
        clearInterval(globalTimer)
        // globalTimer = setInterval(()=> {
        //     this.fixHeadWidth()
        // }, 300)
    },
    watch: {
        selected (value) {
            this.emit('selected-changed', value)
            var selector = this.$el.querySelector('.row-select')
            if (selector) {
                selector = selector.__vue__
                selector.setOptions({
                    selected: this.selected,
                    items: this.items
                })
            }
        }
    },
    components: {
        TableHead
    },
    methods: {
        click (event) {
            var option = event.target.dataset;
            var target = event.target.closest('td');
            this.emit('td-click', target);
            if (this.action && option.action 
                && this.action.hasOwnProperty(option.action)) {
                var row = target.dataset.row;
                event.data = this.items[+row];
                event.field = target.dataset.col;
                event.option = option;
                this.action[option.action].call(this, event);
            }
        },
        lockHeader (e) {
            this.scrollTop = e.target.scrollTop
        },
        changeCheck (e) {
            if (e.checkAll) {
                this.selected = this.items.map((item, idx) => {return idx})
            }
            else {
                this.selected = [];
            }
        },
        fixHeadWidth () {
            let headRow = this.$el.querySelector('thead.head-row')
            let fistRow = this.$el.querySelector('.table-row')
            if (headRow && fistRow) {
                let headCells = headRow.querySelectorAll('td')
                let tdList = fistRow.querySelectorAll('td')
                for (let i = 0; i < tdList.length; i++) {
                    let width = tdList[i].clientWidth
                    headCells[i].style.width = width + 'px'
                }
            }
        },
        setFields (fields) {
            this.fields = fields;
        },
        setItems (items) {
            this.items = items;
        },
        getSelected () {
            return this.selected || [];
        },
        setSelected (value) {
            this.selected = value;
        },
        cellFilter (key, item) {
            var value = item[key];
            var cellTextMap = this.cellTextMap;
            if (cellTextMap && typeof cellTextMap[key] === 'function') {
                return item && cellTextMap[key].call(this, item[key], item, key);
            } else if (cellTextMap && cellTextMap.fieldFilter) {
                // 如果没有对应字段处理函数，使用默认通用字段解析函数（fieldFilter）
                return item && cellTextMap.fieldFilter.call(this, item[key], item, key);
            }
            else {
                return value === null ? (this.emptyText || '-') : item[key];
            }
        },

        cellPlugin (field, item) {

        }
    }
};

</script>

<style type="text/css" lang="less">
.simple-table {
    border: 1px solid #e4e4e4;
    border-top: none;
    border-right: none;
    position: relative;

    .plugin-items {
        display: inline-block;
        position: absolute;
        right: 5px;
        top: 10px;
    }

    .table-body.scroll {
        max-height: 80vh;
        overflow: scroll;
    }
    thead.head-row {
        width: 100%;
        position: static;
        background: #EEE;
    }
    table {
        border-spacing: 0;
        width: 100%;
    }
    thead {
        background-color: #f5f5f5;
        tr td {
            line-height: 2em;
            vertical-align: baseline;
            background: #EEE;
        }
    }

    td[data-col="select"] {
        width: 50px;
    }
    td {
        color: #333;
        font-size: 12px;
        line-height: 1.5em;
        border-top: 1px solid #e4e4e4;
        border-right: 1px solid #e4e4e4;
        padding: 8px 5px;
        position: relative;
        vertical-align: middle;
    }
}
</style>



