<template>
<div class="city-picker">
    <select-list 
        :params="{items: provinceList, value: province}"
        @value-change="changeProvince">
    </select-list>
    <select-list
        :params="{items: cityList, value: city}"
        @value-change="changeCity"
        v-if="cityList.length"> 
    </select-list>
</div>
</template>

<script type="text/javascript">
import SelectList from '../SelectList'
import ui from '../base/ui'
import * as CityInfo from "cn-city"

const params = {
    province: '',
    city: '',
    cityName: '',
    cityList: []
}

export default {
    mixins: [ui],
    params: params,
    ready () {
        if (this.province !== '') {
            this.getCityList()
        }
    },
    computed: {
        provinceList() {
            var list = []
            for (let code in CityInfo.province) {
                list.push({
                    value: code,
                    name: CityInfo.province[code]
                })
            }
            return list
        }
    },
    watch: {
        province() {
            this.getCityList()
        },
        cityName (value) {
            // 是否需要支持反向查询
        }
    },
    methods: {
        changeProvince (e) {
            this.province = e.data.value
            this.emit('province-change', e.data)
        },
        changeCity (e) {
            this.city = e.data.value
            this.emit('city-change', e.data)
        },
        getCityList () {
            var list = []
            var idList = CityInfo.cityMap[this.province]
            this.cityList = idList.map((id) => {
                return {
                    value: id,
                    name: CityInfo.city[id]
                }
            })
            this.city = this.cityList[0].value
        }
    },
    components: {
        SelectList
    }
}

</script>


