<template>
<div class="dialog-panel-demo demo">
    <div class="section">
        <h2 class="demo-title">简单弹窗</h2>
        <div class="box">
            <dialog-panel ref="dialog">
                <div slot="content">弹窗内容</div>
            </dialog-panel>
        </div>
        <div class="box">
            <dialog-panel ref="dialog2">
                <div slot="title">自定义标题</div>
                <div slot="content">自定义内容</div>
            </dialog-panel>
        </div>
    </div>

    <div class="section">
        <h2 class="demo-title">嵌入内容组件</h2>
        <dialog-panel ref="dialog3">
            <div slot="title">登记表</div>
            <div slot="content">
                <post-form></post-form>
            </div>
        </dialog-panel>
    </div>

    <div class="section">
        <h2 class="demo-title">显示模态弹窗</h2>
        <div class="box">
            <button @click="showDialog">显示模态弹窗</button>
        </div>
    </div>
</div>
</template>
<script type="text/javascript">
import Dialog from 'smui/Dialog'
import DialogPanel from 'smui/DialogPanel'
import PostForm from './PostForm.vue'
import RichTable from './RichTable.vue'

export default {
    name: 'Dialog',
    desc: '弹窗是一种信息容器',
    data() {
        return {

        }
    },
    mounted() {
        this.$refs.dialog2.title = 'xxx'
        this.$refs.dialog3.title = 'xxx'
    },
    methods: {
        showDialog() {
            new Dialog({
                data: {
                    width: 900,
                    title: '查看信息'
                },
                content: RichTable
            }).showModal()
        }
    },
    components: {DialogPanel, PostForm}
}
</script>

<style type="text/css" lang="less">
.dialog-panel-demo {
    min-height: 500px;
    .dialog {
        position: static;
        border: 1px solid #CCC;
        box-shadow: 5px 5px #EEE;
        display: block;
        margin: 20px;
    }
}
</style>



