idator.check\n\n\nvar params = {\n    item: {\n        title: '标题',\n        desc: '描述',\n        url: 'http://e.sm.cn'\n    }\n}\n\nexport default {\n    name: 'PostForm-demo',\n    desc: '表单支持字段自动提示、校验',\n    params,\n    mixins: [ui],\n    components: {FieldTip, PostForm, Info},\n    data() {\n        return {}\n    },\n    methods: {\n        change(e) {\n            validator.check(e.value)\n        },\n        submit() {\n            if (this.$refs.form.validate()) {\n                Dialog.alert('提交成功')\n            }\n        }\n    }\n}\n\n</script>\n\n<style type=\"text/css\" lang=\"less\">\n.test-form {\n    height: 300px;\n    input,textarea {\n        width: 150px;\n        line-height: 20px;\n    }\n\n    form {\n        border: 1px solid #CCC;\n        border-radius: 5px;\n        padding: 10px;\n        background-color: #FFF;\n    }\n\n    form:hover {\n        box-shadow: 3px 5px #EEE;\n    }\n\n    .line {\n        line-height: 30px;\n        padding: 5px 10px;\n        label {\n            width: 5em;\n            vertical-align: top;\n            display: inline-block;\n            vertical-align: top;\n        }\n    }\n}\n</style>\n"


