<template>
<div>
    <ul class="color-list">
        <li v-for="(name , color) in colors" :style="bgColor(color)">{{name}} {{color}}</li>
    </ul>
</div>
</template>

<script type="text/javascript">

export default {
    data() {
        return {
            colors: {
                black: '#333',
                darkgray: '#666',
                gray: '#B0B0B0',
                green: '#26c89',
                red: '#E55555'
            }
        }
    },
    methods: {
        bgColor(color) {
            return {
                backgroundColor: color
            }
        }
    }
}
</script>

<style type="text/css" lang="less">
.color-list {
    li {
        line-height: 30px;
        color: #FFF;
        padding: 10px;
        margin: 25px auto;
        width: 78%;
        border-radius: 2px;
    }
}
</style>



