<template>
<div class="drop-list-demo demo">
<div class="section">
    <h2 class="demo-title">选择列表 SelectList</h2>
    <div class="box">
        <md>
            选择列表保留原生Select特性，支持slot设置和items传值
        </md>
        <div class="comp-link">
            相关组件： <a href="#/DropList">下拉列表</a>
        </div>
    </div>
</div>

<div class="section">
    <h2 class="demo-title">选择列表 SelectList</h2>
    <div class="box">
        <h2>选择组件</h2>
        <select-list 
            :params="{value: 0}"
            @value-change="changeType">
            <option :value="1">全选</option>
            <option :value="0">单选</option>
        </select-list>
    </div>
</div>

</div>
</template>

<script type="text/javascript">
import SelectList from 'smui/SelectList'

export default {
    data() {
        return {
            selectInfo: {},
            params: {
                content: ''
            }
        }
    },
    methods: {
        changeType (e) {
            this.$refs.batch.disabled = !!e.data.value
        }
    },
    components: {
        SelectList
    }
}
</script>


