<template>
<div class="drop-list-demo demo">
<div class="section">
    <h2 class="demo-title">下拉列表 DropList</h2>
    <div class="box">
        <md>
            下拉列表
            - command模式，选择执行动作
            - select模式，选择单个参数
            - MultiSelect, 选择多个参数
        </md>
        <div class="comp-link">
            相关组件： <a href="#/SelectList">选择列表</a>
        </div>
    </div>
</div>

<div class="section">
    <h2 class="demo-title">下拉列表组件</h2>
    <div class="box">
        <drop-list 
            :params="{mod:'command', title: '批量操作'}"
            @select-change="changeCommand" ref="batch">
            <ul>
                <li>删除</li>
                <li>修改</li>
                <li>启用</li>
                <li @click="pause">暂停</li>
            </ul>
        </drop-list>
        <drop-list :params="{mod:'select', title: '选择状态'}"
            @select-change="changeStatus">
            <ul>
                <li :value="1">已开始</li>
                <li :value="2">已删除</li>
            </ul>
        </drop-list>
    </div>
</div>

<div class="section">
    <h2 class="demo-title">选择列表 SelectList</h2>
    <div class="box">
        <h2>选择组件</h2>
        <select-list 
            :params="{value: 0}"
            @value-change="changeType">
            <option :value="1">全选</option>
            <option :value="0">单选</option>
        </select-list>
    </div>
</div>

</div>
</template>

<script type="text/javascript">
import DropList from 'smui/DropList'
import SelectList from 'smui/SelectList'
import Dialog from 'smui/Dialog'

export default {
    data() {
        return {
            selectInfo: '',
            params: {
                content: ''
            }
        }
    },
    methods: {
        changeCommand (e) {
            Dialog.confirm(`确认要[${e.data.name}]所选物料？`)
        },
        changeStatus (e) {
            Dialog.confirm(`选择状态为[${e.data.name}]的物料？`)
        },
        pause (e) {
            Dialog.toast({message: '暂停'})
        },
        changeType (e) {
            this.$refs.batch.disabled = !!e.data.value
        }
    },
    components: {
        DropList,
        SelectList
    }
}
</script>


