<template>
<div class="test-form demo">

<div class="section conduct">
    <h2 class="demo-title">表单容器组件</h2>
    <div class="box">
        <md>
            - 表单校验
            - 统一布局
        </md>
    </div>
</div>

<div class="section">
<post-form ref="form">
    <div class="line">
        <label>标题</label>
        <input type="text" data-field="{name: 'title'}" maxlength="32">
        <field-tip :params="{left: 30}"></field-tip>
        <info>
            标题：描述XXX
        </info>
    </div>
    <div class="line">
        <label>描述</label>
        <textarea data-field="{name: 'desc'}"
            id="desc-text"
            maxlength="64"></textarea>
        <field-tip :params="{left: 30, for: 'desc-text'}"></field-tip>
    </div>
    <div class="line">
        <label>预算</label>
        <input type="number" data-field="{name: 'budget'}" max="999" min="10" step="0.1">元
        <field-tip :params="{left: 30}"></field-tip>
    </div>
    <div class="line">
        <label>URL</label>
        <input type="text" 
            data-field="{name: 'url'}"
            maxlength="128">
        <field-tip :params="{left: 30}"></field-tip>
    </div>
    <div class="line">
        <button @click="submit">确定</button>
    </div>
</post-form>
</div>

</div>
</template>

<script type="text/javascript">
import ui from 'smui/base/ui'
import FieldTip from 'smui/FieldTip'
import Field from 'smui/directive/field'
import validator from 'wali'
import PostForm from 'smui/PostForm'
import Info from 'smui/Info'
import Dialog from 'smui/Dialog'

validator.extendRules({
    title: {
        maxByteLength: {
            value: 12,
            message: '标题最多12个字符'
        },
        minByteLength: {
            value: 1,
            message: '标题不能为空'
        }
    }
})

validator.setSiteUrls('http://e.sm.cn/')

// 挂载 validator
Field.check = validator.check


var params = {
    item: {
        title: '标题',
        desc: '描述',
        url: 'http://e.sm.cn'
    }
}

export default {
    name: 'PostForm-demo',
    desc: '表单支持字段自动提示、校验',
    params,
    mixins: [ui],
    components: {FieldTip, PostForm, Info},
    data() {
        return {}
    },
    methods: {
        change(e) {
            validator.check(e.value)
        },
        submit() {
            if (this.$refs.form.validate()) {
                Dialog.alert('提交成功')
            }
        }
    }
}

</script>

<style type="text/css" lang="less">
.test-form {
    height: 300px;
    input,textarea {
        width: 150px;
        line-height: 20px;
    }

    form {
        border: 1px solid #CCC;
        border-radius: 5px;
        padding: 10px;
        background-color: #FFF;
    }

    form:hover {
        box-shadow: 3px 5px #EEE;
    }

    .line {
        line-height: 30px;
        padding: 5px 10px;
        label {
            width: 5em;
            vertical-align: top;
            display: inline-block;
            vertical-align: top;
        }
    }
}
</style>



