<template>
<div class="tree-demo demo">
<div class="section">
    <h2 class="demo-title">Tree</h2>
    <div class="box">
        <md>
            导航、选择
        </md>
    </div>
</div>

<div class="section">
    <h2 class="demo-title">Tab 片标签</h2>
    <tree :params="params"
            @tab-change="changeValue">    
    </tree>
    <div class="box">
        {{info}}
    </div>
</div>

</div>
</template>

<script type="text/javascript">
import Tree from 'smui/Tree'

const tabList = [
    {name: '表格', tab: 'table'},
    {name: '表单', tab: 'form'},
    {
        name: '消息',
        tab: 'dialog',
        subTab: 'message',
        children: [
            {name: '消息', tab: 'message'},
            {name: '弹窗', tab: 'dialog'}
        ]
    }
]
let treeData = [
    {
        name: '前端',
        link: '#front',
        children: [
            {name: 'JS', link:'#js'},
            {name: 'HTML', link:'#html'},
            {name: 'CSS', link:'#css'}
        ]
    },
    {
        name: 'Java',
        link: '#front',
        children: [
            {name: 'Java', link:'#js'},
            {name: 'J2EE', link:'#html'},
            {name: 'J2ME', link:'#css'}
        ]
    },
    {
        name: 'PHP',
        link: '#front',
        children: [
            {name: 'php', link:'#js'},
            {name: 'smarty', link:'#html'},
            {name: 'fuel', link:'#css'}
        ]
    }
]

export default {
    data() {
        return {
            info: {},
            params: {
                items: treeData
            }
        }
    },
    methods: {
        changeValue (e) {
            this.info = e.data
        }
    },
    components: {
        Tree
    }
}
</script>



