<template>
<div class="stepper">
<ul>
    <li v-for="(item, index) in items" >
        <div class="badget" :class="{active: value > index }"
            @click="changeValue(index + 1)">{{index + 1}}</div>
        <div class="text">{{item.name}}</div>
        <div class="line"></div>
    </li>
</ul>
</div>
</template>
<script type="text/javascript">
import ui from '../base/ui'

const params = {
    value: 1,
    items: []
}

export default {
    params,
    mixins: [ui],
    methods: {
        changeValue (value) {
            this.value = value
            this.emit('value-change', {value: this.value})
        }
    }
}
</script>

<style type="text/css" lang="less">
.stepper {
    line-height: 25px;

    li {
        list-style: none;
        display: inline-block;
    }
    .badget {
        display: inline-block;
        width: 25px;
        height: 25px;
        line-height: 25px;
        text-align: center;
        border-radius: 12.5px;
        background-color: #CCC;
        color: #FFF;
    }

    .badget:hover,
    .badget.active {
        background-color: #26c899;
    }

    .text {
        display: inline-block;
    }
    .line {
        width: 100px;
        border: 0;
        height: 1px;
        vertical-align: middle;
        margin: 0 5px;
        background-color: #CCC;
        display: inline-block;
    }
    li:last-child {
        .line {
            display: none;
        }
    }
}
</style>


