<template>
<div class="code-editor">
    <div class="box left" @click="expand(false)">
        <textarea id="text-code">{{source}}</textarea>
    </div>
    <div :class="{expand: isExpand}" class="box right" @click="expand(true)">
        <div class="result-panel">
            
        </div>
    </div>
</div>
</template>

<script type="text/javascript">
import * as Babel from 'babel-standalone'
import LineBox from './LineBox'
import CodeMirror from 'codemirror/lib/codemirror'   
import 'codemirror/lib/codemirror.css'
import 'codemirror/mode/css/css'
import 'codemirror/mode/xml/xml'
import 'codemirror/mode/vue/vue'
import 'codemirror/addon/mode/overlay'
import 'codemirror/mode/javascript/javascript'
import 'codemirror/mode/htmlmixed/htmlmixed'
import less from 'less'
import * as smui from 'smui'

// var fileCode = require("raw-loader!mirror/Calendar.vue");

window.require = __webpack_require__
window.less = less

var VModule = {
    require (id) {
        if (id.match(/smui/g)) {
            var key = id.split('/').pop()
            return smui[key] || {}
        }
        return require(id)
    },
    exports: {
        script: '',
        style: '',
        template: ''
    },
    compileLess () {
        if (!this.exports.style) {
            return
        }
        less.render(
            this.exports.style,
            {
                  paths: ['.'],
                  filename: 'style.less',
                  compress: false
            },
            function (e, output) {
                document.querySelector('#temp-style').textContent = output.css
            }
        )
    },
    create (option) {
        var VModule = this
        eval(this.exports.script)
        var obj = this.exports.default || this.exports
        var x = Vue.compile(this.exports.template)
        this.compileLess()
        obj.replace = false
        Object.assign(obj, x, option)
        return Vue.extend({extends: obj})
    }
}

window.VModule = VModule


window.compiler = {
    getBlock (content, block) {
        let regs = new RegExp(`<${block}[^>]*>([.\\D\\W\d\\w\\s]*)</${block}>`)
        let matches = content.match(regs)
        if (matches && matches.length > 1) {
            return matches[1]
        }
    },
    compile (content) {
        let result = {
            template: this.getBlock(content, 'template'),
            script: this.getBlock(content, 'script'),
            style: this.getBlock(content, 'style')
        }
        if (!result.script && !result.template) {
            result.script = content
        }
        return result
    }
}

export default {
    data() {
        return {
            source: '',
            output: '',
            isExpand: false,
        }
    },
    watch: {
        source (value) {
            this.compileCode()
        }
    },
    mounted() {
        this.initEditor()
        setTimeout(()=>{this.ensure()}, 300);
    },
    methods: {
        compileCode(e) {
            let source = compiler.compile(this.source)
            try {
                source.script = Babel.transform(
                    source.script, {presets: ["es2015"]}
                ).code

                this.output = `(function (require, exports, module) {\n${source.script}\n})(VModule.require, VModule.exports, VModule)`

                source.script = this.output
                Object.assign(VModule.exports, source)
            }
            catch(e) {
                this.output = ''
            }
        },
        expand(flag) {
            this.isExpand = flag
        },
        ensure() {
            var App = VModule.create({})
            var p = document.querySelector('.fixed-dialog .right')
            p.innerHTML = '<div class="result-panel"></div>'
            new App({el: '.fixed-dialog .result-panel', replace: false})
            this.expand(true)
        },
        initEditor () {
            var $vm = this
            var mixedMode = {
                name: 'htmlmixed',
                scriptTypes: [{
                    matches: /\/x-handlebars-template|\/x-mustache/i,
                    mode: null
                }]
            }
            
            var textCode = document.getElementById("text-code")
            this.codeContent = textCode.value = textCode.textContent.trim()
            
            var editor = CodeMirror.fromTextArea(
                textCode,
                {
                    mode: mixedMode,
                    selectionPointer: true,
                    lineNumbers: true,
                    tabSize: 4,
                    matchBrackets: true,
                    styleActiveLine: false
                }
            )

            editor.on('change', function() {
                $vm.source = editor.getValue()
                $vm.compileCode()
            })

            window.editor = editor
            $vm.compileCode()

        }
    },
    components: {
        LineBox
    }
}
</script>

<style type="text/css" lang="less">
.CodeMirror.CodeMirror {
    height: 550px !important;
}
.code-editor {
    min-width: 95vw;
    position: relative;
    .box {
        display: inline-block;
        width: 60%;
    }
    .box.right {
        min-height: 600vh;
        width: 40%;
        vertical-align: top;
        border-left: 1px solid #CCC;
        background-color: #EEE;
        position: absolute;
        right: 0;
        overflow: scroll;
        padding: 10px; 
    }
    .box .demo {
        max-height: 90vh;
        overflow-y: scroll;
    }
    .result-panel {
        min-width: 500px;
    }
    .right.expand {
        width: 80vw!important;
        z-index: 7;
    }
    .CodeMirror-line {
        padding-left: 30px;
    }

    .result-panel {
        display: block;
    }
}
</style>


