module.exports = "<template>\n<div class=\"app-demo demo\">\n    <div class=\"section conduct\">\n        <h2 class=\"demo-title\">表格组件</h2>\n        <div class=\"box\">\n            提供列表数据展现\n        </div>\n        <div class=\"comp-link box\">\n            <a href=\"#/Pager\">翻页组件</a>\n            <a href=\"#/DatePicker\">日历组件</a>\n        </div>\n    </div>\n    <div class=\"section\">\n        <h2 class=\"demo-title\">表格使用例子</h2>\n        <div class=\"header-line box\">\n            <button @click=\"createPlan\">+ 新建文章</button>\n            <drop-list :params=\"{mod:'command', title: '批量操作', disabled: true}\"\n                @select-change=\"changeCommand\" ref=\"batch\">\n                <ul>\n                    <li>删除</li>\n                    <li>修改</li>\n                    <li>启用</li>\n                    <li @click=\"op\">暂停</li>\n                </ul>\n            </drop-list>\n            <drop-list :params=\"{mod:'select', title: '选择状态'}\"\n                @select-change=\"changeStatus\">\n                <ul>\n                    <li :value=\"1\">已开始</li>\n                    <li :value=\"2\">已删除</li>\n                </ul>\n            </drop-list>\n            <div class=\"pad-wrapper\">\n                <date-picker></date-picker>\n            </div>\n        </div>\n        <div class=\"box\">\n            <rich-table :params=\"tableConf\"\n                @selected-changed=\"changeSelected\" ref=\"table\">\n            </rich-table>\n            <pager\n                :params=\"pageInfo\"\n                @page-change=\"changePage\"\n                @size-change=\"changePage\"\n                @list-change=\"changeList\">\n            </pager>\n        </div>\n    </div>\n</div>\n</template>\n\n<script type=\"text/javascript\">\nimport tableConf from './tableConf'\nimport RichTable from 'smui/RichTable'\nimport DropList from 'smui/DropList'\nimport DatePicker from 'smui/DatePicker'\nimport Pager from 'smui/Pager'\nimport Dialog from 'smui/Dialog'\n\nimport Form from './PostForm.vue'\n\nlet dataList = new Array(100).fill(1).map(\n    (item, idx) => {\n        return {\n            title: 'user' + idx,\n            author: Math.ceil(Math.random() * 1000).toString(32),\n            visitor: idx + 5\n        }\n    }\n);\n\nexport default {\n    name: 'RichTable-demo',\n    desc: '表格用于展现列表数据',\n    data () {\n        return {\n            selected: [],\n            tableConf: tableConf,\n            pageInfo: {\n                pageSize: 20,\n                items: dataList,\n                sizeOptions: [10, 20, 50],\n                currentPage: 1\n            }\n        }\n    },\n    methods: {\n        createPlan () {\n            let dialog = new Dialog({\n                data: {\n                    width: 700\n                },\n                content: Form\n            })\n\n            dialog.showModal()\n\n            dialog.$on('ensure', function () {\n                Dialog.alert('新建成功', () => {dialog.close()} )\n            })\n        },\n        changeList (e) {\n            Object.assign(this.pageInfo, e.data)\n            if (this.$refs.table) {\n                this.$refs.table.items = e.data.list;\n            }\n        },\n        changePage (e) {\n            Object.assign(this.pageInfo, e.data)\n            if (this.$refs.table) {\n                this.$refs.table.setSelected([]);\n            }\n        },\n        changeCommand (e) {\n            Dialog.confirm(`确认要[${e.data.name}]所选物料？`)\n        },\n        changeStatus (e) {\n            Dialog.confirm(`选择状态为[${e.data.name}]的物料？`)\n        },\n        changeSelected (e) {\n            this.selected = e.data\n            // 也可以直接双向绑定batch.disabled属性，但是不建议这么做\n            this.$refs.batch.disabled = e.data.length === 0\n        },\n        op(e) {\n\n        }\n    },\n    components: {\n        DropList, RichTable, Pager, DatePicker\n    }\n}\n\n</script>\n\n<style type=\"text/css\" lang=\"less\">\n\n.app-demo {\n    padding: 10px;\n    td i {\n        cursor: pointer;\n        float: right;\n    }\n    .header-line {\n        height: 50px;\n        line-height: 30px;\n    }\n    .date-picker {\n        display: inline-block;\n    }\n    .pad-wrapper {\n        width: 250px;\n        display: inline-block;\n    }\n}\n</style>\n"


