<template>
<div>
    <ul class="fonticon-list">
        <li v-for="icon in icons">
            <i :class="icon"></i>
            <i :class="icon" class="xicon"></i>
            <div>{{icon}}</div>
        </li>
    </ul>
</div>
</template>

<script type="text/javascript">

const icons = [
    "sm-diagram","sm-reorder","sm-reorder-up","sm-reorder-down",
    "sm-arrow-down","sm-arrow_up","sm-arrow-right","sm-arrow-left",
    "sm-arrow-dropdown","sm-arrow-dropdown-right","sm-info","sm-question",
    "sm-calender","sm-plan","sm-download","sm-add","sm-delete","sm-start",
    "sm-stop","sm-cancel","sm-edit","sm-search","sm-addall","sm-avator",
    "sm-bulb","sm-close","sm-copy","sm-cycle","sm-loading","sm-share"
]

export default {
    data() {
        return {
            icons: icons
        }
    },
    methods: {
        bgColor(color) {
            return {
                backgroundColor: color
            }
        }
    }
}
</script>

<style type="text/css" lang="less">
.fonticon-list {
    li {
        display: inline-block;
        padding: 10px;
        width: 150px;
        height: 50px;
        margin: 10px;
        vertical-align: top;
        border: 1px solid #EEE;
        position: relative;
    }

    .xicon {
        display: none;
        position: absolute;
        color: #26C890;
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        background: #FFF;
        text-align: center;
        line-height: 50px;
        font-size: 3em;
    }

    li:hover {
        .xicon {
            display: block;
        }
    }
}
</style>



